"use client"
import { useEffect, useRef } from 'react'

/**
 * MeasurementRail — a thin 2px vertical bar on the left edge.
 * Fills proportionally as the user scrolls the page.
 * Only visible on xl screens. Respects prefers-reduced-motion.
 */
export default function MeasurementRail() {
  const fillRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const mq = window.matchMedia('(prefers-reduced-motion: reduce)')
    if (mq.matches) return

    const update = () => {
      const scrollTop = window.scrollY
      const docHeight = document.documentElement.scrollHeight - window.innerHeight
      const ratio = docHeight > 0 ? Math.min(scrollTop / docHeight, 1) : 0
      if (fillRef.current) {
        fillRef.current.style.height = `${ratio * 100}%`
      }
    }

    window.addEventListener('scroll', update, { passive: true })
    update()
    return () => window.removeEventListener('scroll', update)
  }, [])

  return (
    <div
      className="measurement-rail"
      aria-hidden="true"
      role="presentation"
    >
      <div ref={fillRef} className="measurement-rail-fill" />
    </div>
  )
}
