"use client"
import { projects as allProjects } from '../data/projects'
import ProjectCard from './ProjectCard'

export default function SelectedWork({ filter }: { filter?: string }) {
  const minShow = 4
  const maxShow = 6

  let list = filter
    ? allProjects.filter(p => p.engagementModes.includes(filter))
    : allProjects.slice(0, 4)

  if (filter) {
    if (list.length < minShow) {
      const extras = allProjects.filter(p => !list.some(lp => lp.slug === p.slug))
      for (const e of extras) {
        list.push(e)
        if (list.length >= minShow) break
      }
    }
    list = list.slice(0, maxShow)
  }

  return (
    <div>
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-2 xl:grid-cols-4 gap-5" role="list">
        {list.map(p => (
          <div key={p.slug} role="listitem">
            <ProjectCard project={p} />
          </div>
        ))}
      </div>
    </div>
  )
}

